#product1 {
    text-align: center;

    .pro-container {
        display: flex;
        justify-content: space-between;
        padding-top: 20px;
        flex-wrap: wrap;

        .pro {
            width: 23%;
            min-width: 250px;
            padding: 10px 12px;
            border: 1px solid #cce7d0;
            border-radius: 25px; 
            cursor: pointer;
            box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
            margin: 15px 0;
            transition: 0.2s ease;
            position: relative;
    
            img {
                width: 100%;
                border-radius: 20px;
            }
    
            .des {
                text-align: start;
                padding: 10px 0;
    
                span {
                    color: #606063 ;
                    font-size: 12px;
                }
    
                h5 {
                    padding-top: 7px;
                    color: #1a1a1a;
                    font-size: 14px;
                }

                h5.category {
                    text-transform: capitalize;
                }
    
                i {
                    font-size: 12px;
                    color: rgb(243, 181, 25);
                }
    
                h4 {
                    padding-top: 7px;
                    font-size: 15px;
                    font-weight: 700;
                    color: #088178; 
                }
            }
    
            .cart {
                width: 40px;
                height: 40px;
                line-height: 40px;
                border-radius: 50px;
                background-color: #e8f6ea;
                font-weight: 500;
                color: #088178;
                border: 1px solid #cce7d0;
                position: absolute;
                bottom: 20px;
                right: 10px;
            }
        }
    
        .pro:hover {
            box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.1);
        }
    }

    
}

@media(max-width: 830px) {
    #product1 {
        .pro-container {
            justify-content: center;

            .pro {
                margin: 15px;
            }
        }
    }
}

@media(max-width: 477px) {
    #product1 {
        .pro-container {
            .pro {
                width: 100%;
            }
        }
    }
}